import React from 'react';
import '../../css/Contact.css'
import TitleComponent from '../TitleComponent';
import { Form, Input, Button, message } from 'antd';
import $ from 'jquery';

const { TextArea } = Input;

const Contact = props => {
    return (
        <section id="contact" style={{position:'relative'}}>
            <svg preserveAspectRatio="none" viewBox="0 0 100 102" height="75" width="100%" version="1.1" xmlns="http://www.w3.org/2000/svg" class="svgcolor-light" style={{position:'absolute', top:0, left:0}}>
                <path d="M0 0 L50 100 L100 0 Z" fill="white" stroke="white"></path>
            </svg>
            <div className="contact container flex">
                <TitleComponent title="contact" />
                <div className="animated slide-in-right trigger" trigger="slide-in-right" style={{ color: '#FF8D00', fontSize: '1.5rem' }}>
                    Have a question or want to work together?
                </div>
                <Form className="animated popIn contact-input-container trigger" trigger="popIn" onFinish={values => {
                    const { name, email, msg } = values;
                    $.ajax({
                        url: 'https://formspree.io/f/mlealapy',
                        method: 'POST',
                        data: {
                            message: `${name}님께서 보낸 메세지.\nEmail : ${email}\n내용 : ${msg}`
                        },
                        dataType: 'json'
                    }).done(function (response) {
                        if (response.ok) {
                            message.success('이메일 발송에 성공하였습니다.')
                        } else {
                            message.error('이메일 발송에 실패하였습니다.')
                        }
                    });
                }}>
                    <Form.Item name="name">
                        <Input placeholder="Name" />
                    </Form.Item>
                    <Form.Item name="email">
                        <Input placeholder="Enter Email" type="email" />
                    </Form.Item>
                    <Form.Item name="msg">
                        <TextArea placeholder="Your Message" style={{ minHeight: '150px' }} />
                    </Form.Item>
                    <button type="submit" className="btn-submit">
                        SUBMIT
                    </button>
                </Form>
            </div>
        </section>
    )
}

export default Contact;